/**
 * Safe fetch utility that never throws uncaught errors
 * Uses XMLHttpRequest to avoid browser extension interference
 */

/**
 * Performs a safe HTTP request using XMLHttpRequest
 * @param {string} url - The URL to fetch
 * @param {object} options - Request options (method, headers, body)
 * @param {number} timeout - Timeout in milliseconds (default: 5000)
 * @returns {Promise<{ok: boolean, status: number, statusText: string, data: any, error: string|null}>}
 */
export const safeFetch = (url, options = {}, timeout = 5000) => {
  return new Promise((resolve) => {
    const xhr = new XMLHttpRequest();
    const method = options.method || 'GET';
    
    const timeoutId = setTimeout(() => {
      xhr.abort();
      console.warn(`‚ö†Ô∏è Request timeout: ${url}`);
      resolve({
        ok: false,
        status: 0,
        statusText: 'Timeout',
        data: null,
        error: 'Request timeout',
      });
    }, timeout);
    
    xhr.onload = () => {
      clearTimeout(timeoutId);
      let data = null;
      let parseError = null;
      
      try {
        if (xhr.responseText) {
          data = JSON.parse(xhr.responseText);
        }
      } catch (e) {
        console.warn('Failed to parse response:', e);
        parseError = 'Failed to parse response';
      }
      
      resolve({
        ok: xhr.status >= 200 && xhr.status < 300,
        status: xhr.status,
        statusText: xhr.statusText,
        data: data,
        error: parseError || (xhr.status >= 200 && xhr.status < 300 ? null : xhr.statusText),
      });
    };
    
    xhr.onerror = () => {
      clearTimeout(timeoutId);
      console.warn(`‚ö†Ô∏è Request failed: ${url}`);
      resolve({
        ok: false,
        status: 0,
        statusText: 'Network Error',
        data: null,
        error: 'Network error',
      });
    };
    
    xhr.onabort = () => {
      clearTimeout(timeoutId);
      resolve({
        ok: false,
        status: 0,
        statusText: 'Aborted',
        data: null,
        error: 'Request aborted',
      });
    };
    
    try {
      xhr.open(method, url, true);
      
      // Set headers
      if (options.headers) {
        Object.entries(options.headers).forEach(([key, value]) => {
          xhr.setRequestHeader(key, value);
        });
      }
      
      // Send request
      if (options.body) {
        xhr.send(options.body);
      } else {
        xhr.send();
      }
    } catch (error) {
      clearTimeout(timeoutId);
      console.warn(`‚ùå Request error: ${url}`, error.message);
      resolve({
        ok: false,
        status: 0,
        statusText: 'Error',
        data: null,
        error: error.message,
      });
    }
  });
};

/**
 * Helper function to check backend health
 * @param {string} baseURL - The base URL of the API
 * @returns {Promise<boolean>} - Returns true if backend is available
 */
export const checkBackendHealth = async (baseURL) => {
  const result = await safeFetch(`${baseURL}/state`, {
    method: 'GET',
    headers: { 'Content-Type': 'application/json' },
  }, 5000);
  
  if (result.ok) {
    console.log('‚úÖ Backend is available');
    return true;
  } else {
    console.warn('‚ö†Ô∏è Backend is not reachable:', result.error);
    return false;
  }
};

/**
 * Creates error suppression handlers for window events
 * Call the returned cleanup function when component unmounts
 * @returns {Function} Cleanup function to remove event listeners
 */
export const setupErrorSuppression = () => {
  const handleError = (event) => {
    if (event.message && event.message.includes('Failed to fetch')) {
      const stack = event.error?.stack || '';
      if (stack.includes('/state') || 
          stack.includes('checkBackendHealth') || 
          stack.includes('safeFetch') ||
          !stack) {
        console.warn('üîá Suppressed fetch error');
        event.preventDefault();
        event.stopPropagation();
        event.stopImmediatePropagation();
        return false;
      }
    }
  };
  
  const handleUnhandledRejection = (event) => {
    const errorMsg = event.reason?.message || event.reason?.toString() || '';
    if (errorMsg.includes('Failed to fetch') || errorMsg.includes('fetch')) {
      const stack = event.reason?.stack || '';
      if (stack.includes('/state') || 
          stack.includes('checkBackendHealth') || 
          stack.includes('safeFetch') ||
          errorMsg.includes('/state') ||
          !stack) {
        console.warn('üîá Suppressed promise rejection');
        event.preventDefault();
        event.stopPropagation();
        event.stopImmediatePropagation();
        return false;
      }
    }
  };
  
  window.addEventListener('error', handleError, true);
  window.addEventListener('unhandledrejection', handleUnhandledRejection, true);
  
  // Return cleanup function
  return () => {
    window.removeEventListener('error', handleError, true);
    window.removeEventListener('unhandledrejection', handleUnhandledRejection, true);
  };
};
